/* フッター ------------------------------*/
.l-footer01 {
    width: 100vw;
    height: calc(522px + 60px);
    // background-color: #d1cfcf;
}
/* ------------------------------*/

.l-footer01__inner {
    width: 1920px;
    height: 522px;

    margin: auto;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}
/* ------------------------------*/
//背景
.footer-color {
    position: relative;
    z-index: 1;
    //なぜかずれる
    width: 1929px;
    transform: translate(4px,0px);

    height: 522px;

    display: block;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

/* ------------------------------*/

/* ナビゲーション */
.l-footer01__navi {
    position: absolute;
    z-index: 100;
    width: auto;

    margin-top: 50px;
}

/* ------------------------------*/
.cp-footer01__items {
    writing-mode: vertical-rl;
    font-size: 18px;
    font-family: "游明朝", "YuMincho", serif;
    font-weight: normal;
    color: #000000;
    letter-spacing: 13px;

    display: flex;
    flex-direction: column;
    list-style: none;
}
/* ------------------------------*/
.cp-footer01__item:nth-child(2) {
    margin-left: 34px;
    margin-right: 34px;
}
/* ------------------------------*/
.cp-footer01__item a {
    display: inline-block;
    transition: 0.2s ease-in-out;
    text-decoration: none;
}
/* ------------------------------*/
.cp-footer01__item a:hover {
    opacity: 0.7;
}

/* ------------------------------*/

/* ロゴ */

.lf-logo-container {
    position: absolute;
    z-index: 100;
    width: 145px;
    height: 92px;

    margin-top: 230px;

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

/* ------------------------------*/

.lf-logo-box {
    width: 40px;
    height: 40px;

    .logo-img {
        width: 100%;
        height: auto;
    }
    .logo-img:hover {
        opacity: 0.7;
        transition: 0.2s ease-in-out;
    }
}

/* ------------------------------*/
.lf-heading {
    font-size: 32px;
    font-family: "游明朝", "YuMincho", serif;
    font-weight: bold;
    color: #000000;
    letter-spacing: 4px;

    margin-top: 20px;
}

/* ------------------------------*/
.l-adress-inner {
    position: absolute;
    z-index: 100;

    margin-top: 391px;
    transform: translateX(5px);
}

.adress-num {
    font-size: 16px;
    font-family: "游明朝", "YuMincho", serif;
    font-weight: normal;
    color: #000000;


}

.adress-txt {
    font-size: 16px;
    font-family: "游明朝", "YuMincho", serif;
    font-weight: normal;
    color: #000000;
    letter-spacing: 3px;
    
    padding-left: 14px;
}

/* ------------------------------*/
.l-tel-inner {
    position: absolute;
    z-index: 100;
    margin-top: 430px;

    transform: translateX(4px);
}


.tel-num {
    font-size: 16px;
    font-family: "游明朝", "YuMincho", serif;
    font-weight: normal;
    color: #000000;
   
}


.fax-num {
    font-size: 16px;
    font-family: "游明朝", "YuMincho", serif;
    font-weight: normal;
    color: #000000;
    margin-left: 14px;
}

/* ------------------------------*/

.l-copylight {
    width: 1920px;
    height: 80px;
    background-color: #000000;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 auto;
}

/* ------------------------------*/

.copylight {
    font-size: 12px;
    font-family: "游明朝", "YuMincho", serif;
    font-weight: bold;
    color: #ffffff;
}

/* ------------------------------*/
